import React from "react";

//UI Material
import { Grid, Stack } from "@mui/material";

//Components
import ActionsCard from "./ActionsCard";
import AboutMeCard from "./AboutMeCard";

//Helpers
import {
  typographyForMainTitles,
  typographyForSubtitles,
  typographyForText,
} from "../Helpers/typography";

export default function Home() {
  // ==========> Component <==========
  return (
    <Grid container spacing={1} justifyContent="center">
      {/**SECTION 1 - HOME */}
      <Grid item xs={12} md={10}>
        {typographyForSubtitles("> Home", "secondary")}
      </Grid>
      <Grid item xs={12} md={10}>
        {typographyForMainTitles(
          "The place where coffee becomes code.",
          "white"
        )}
      </Grid>
      <Grid item xs={12} md={10}>
        <ActionsCard />
      </Grid>
      {/**SECTION 2 - ABOUT ME */}
      <Grid item xs={12} md={10} sx={{marginTop:"20px"}}>
        {typographyForSubtitles("> About Me", "secondary")}
      </Grid>
      <Grid item xs={12} md={10}>
        <Stack justifyContent="flex-start">
          {typographyForMainTitles("I am Fernando Matamoros", "white")}
          {typographyForText(
            "I'm Fernando, a professional web designer & front end developer focused on crafting clean & user friendly experiences.",
            null,
            "white"
          )}
        </Stack>
      </Grid>
      <Grid item xs={12} md={10}>
        <AboutMeCard/>
      </Grid>
      {/**SECTION 3 - HIRE ME*/}
      <Grid item xs={12} md={10} sx={{marginTop:"20px"}}>
        {typographyForSubtitles("> Have An Idea? Let's Build It.", "secondary")}
      </Grid>
      {/**SECTION 4 - CONTACT ME*/}
      <Grid item xs={12} md={10} sx={{marginTop:"20px"}}>
        {typographyForSubtitles("> Any Type Of Query & Discussion", "secondary")}
      </Grid>
    </Grid>
  );
}
